.list {
    width: 100%;
    padding-bottom: 10px;

    &_title {
        width: calc(100% - var(--margin-display));
        margin: 0 auto;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: start;

        font-family: var(--text-3-family);
        font-size: var(--text-3-size);
        font-weight: var(--text-3-weight);
        color: var(--text-3-color);
    }

    &_place {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        row-gap: 14px;
    }
}

.item {
    width: calc(100% - 16px);
    padding: 8px;
    height: 99px;
    background-color: var(--block1);
    border-radius: 4px;
    display: flex;
    justify-content: space-between;
    column-gap: 6px;

    &_info {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        &_title {
            width: 100%;
            display: flex;
            align-items: center;
            column-gap: 3px;
            padding: 3px;

            &_point {
                width: 2px;
                height: 2px;
                background-color: #fff;
                border-radius: 50%;
            }

            &_p1 {
                font-family: var(--text-3-family);
                font-size: var(--text-3-size);
                font-weight: var(--text-3-weight);
                color: var(--text-3-color);

            }
        }

        &_text {
            width: 100%;
            padding: 3px 0;
            height: 33px;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: wrap;

            color: var(--text-2-color);
            font-family: var(--text-2-family);
            font-weight: var(--text-2-weight);
            font-size: var(--text-2-size);


        }

        &_panel {
            width: 100%;
            height: 25px;
            display: flex;
            justify-content: space-between;
            align-items: center;

            &_price {
                font-family: var(--text-4-family);
                font-size: var(--text-4-size);
                font-weight: var(--text-4-weight);
                color: var(--text-4-color);
            }

            &_btns {
                display: flex;
                height: 100%;
                column-gap: 4px;

                &_details {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    height: 100%;
                    padding: 0 15px;
                    background-color: var(--second-color);
                    border-radius: 3px;
                    color: var(--block);
                    font-family: livvic;
                    font-weight: 500;
                    font-size: var(--fz1);
                }
            }
        }
    }

    &_img {
        border-radius: 3px;

    }
}