.footer {
    width: calc(100% - calc(var(--margin-display) * 2));
    padding: 0 var(--margin-display);
    background-color: var(--block);
    position: relative;
    // transform: translateY(-1px);
    padding-bottom: 15px;
    position: relative;

    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: calc(100% - var(--margin-display));
        background-color: var(--block-light);
        height: 1px;
    }

    &_sub {
        width: 100%;
        padding: 20px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        column-gap: 20px;

        &_p1 {
            width: 100%;
            font-family: var(--text-2-family);
            font-size: var(--text-2-size);
            font-weight: var(--text-2-weight);
            color: var(--text-2-color);
        }
    }

    &_list {
        width: 100%;
        display: flex;
        flex-direction: column;
        row-gap: 6px;

        &_item {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;

            &_p1 {
                font-family: var(--text-2-family);
                font-size: var(--text-2-size);
                color: var(--text-2-color);
                font-weight: var(--text-2-weight);
            }
        }
    }

    &_back {
        position: absolute;
        width: 100%;
        height: 50px;
        background-color: red;
        left: 0;
        top: 100%;
    }
}

.header {
    width: calc(100% - calc(var(--margin-display) * 2));
    height: 55px;
    background-color: var(--block);
    padding: 0 var(--margin-display);
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    top: -55px;
    transition-duration: 0.2s;

    &_add {
        top: 0px;
        transition-duration: 0.2s;
    }

    &_menu {
        width: 18px;
        height: 12px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-end;

        &_line {
            width: 100%;
            height: 2px;
            background-color: #fff;
            border-radius: 2px;
        }

        &_lineshort {
            width: 70%;
            height: 2px;
            background-color: #fff;
            border-radius: 2px;
        }
    }

    &_likes {
        width: 25px;
        height: 25px;
        cursor: pointer;
        position: relative;
        z-index: -0.5;

        &_img {
            width: 100%;
            height: 100%;
        }

        &_icon {
            right: 0;
            bottom: 3px;
            position: absolute;
            width: 12px;
            aspect-ratio: 1/1;
            border-radius: 50%;
            background-color: var(--second-color);
            display: flex;
            justify-content: center;
            align-items: center;
            font-family: var(--text-4-family);
            font-size: 7px;
            font-weight: 600;
            color: var(--block);

        }

    }
}