.logo {
    display: flex;
    cursor: pointer;

    &_group {
        display: flex;
        flex-direction: column;
        row-gap: 2px;
        justify-content: center;

        &_p1 {
            font-family: inter;
            font-size: var(--fz3);
            color: #fff;
        }

        &_line {
            width: 100%;
            height: 1px;
            background-color: #fff;
        }
    }
}