.content {
    width: calc(100% - calc(var(--margin-display)));
    height: auto;
    background-color: var(--block);
    padding: 0 calc(var(--margin-display)/2);

    &_title {
        width: 100%;
        height: 60px;
        border-bottom: 1px solid var(--block-light);
        display: flex;
        justify-content: space-between;

        align-items: center;

        &_menu {
            margin-left: calc(var(--margin-display) / 2);

            &_p1 {
                font-family: var(--text-1-family);
                font-size: var(--text-1-size);
                font-weight: var(--text-1-weight);
                color: var(--text-1-color);
            }

            &_line {
                width: 120%;
                height: 1px;
                background-color: #fff;
            }
        }

        &_text {
            font-family: var(--text-2-family);
            font-size: var(--text-2-size);
            font-weight: var(--text-2-weight);
            color: var(--text-2-color);

            width: 55%;
            text-align: end;

        }
    }
}

.background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vh;
    height: 100vh;
    background-color: #000000a6;
    backdrop-filter: blur(1px);
    z-index: 4;
}

.modal {
    padding: calc(40px / 2);
    position: fixed;
    width: calc(100% - calc(40px));
    height: 75vh;
    background-color: var(--block2);
    bottom: 0;
    border-radius: 15px 15px 0 0;
    z-index: 10;
    transition-duration: 200ms;
    display: flex;
    flex-direction: column;
    row-gap: 10px;


    &_top {
        width: 100%;
        height: 30px;
        flex-shrink: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;

        &_p1 {
            font-family: var(--text-3-family);
            font-size: var(--text-3-size);
            font-weight: var(--text-3-weight);
            color: var(--text-3-color);

        }

        &_cross {
            height: 100%;
            aspect-ratio: 1/1;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }

    &_place {
        display: flex;
        flex-direction: column;
        row-gap: 10px;
        overflow: scroll;

    }
}

.massage {
    width: 100%;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: var(--text-4-family);
    font-size: var(--text-4-size);
    font-weight: var(--text-4-weight);
    color: var(--text-4-color);
    text-decoration: underline;
}