.adds {
    width: 100%;
    height: 500px;
    background-color: var(--block);
    position: absolute;
    bottom: 100%;

    &_rev {
        width: 100%;
        height: 500px;
        background-color: var(--block);
        position: absolute;
        left: 0;
        bottom: 100px;
    }
}