.massages {
    width: 100%;
    height: 190px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    row-gap: 14px;

    &_item {
        padding: 10px var(--margin-display);
        width: 60%;
        background-color: var(--block);
        border-radius: 0 3px 3px 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        row-gap: 3px;
        box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.473);

        &_p1 {
            font-family: var(--text-1-family);
            color: var(--text-1-color);
            font-size: var(--text-1-size);
            font-weight: var(--text-1-weight);
        }

        &_p2 {
            font-family: var(--text-2-family);
            color: var(--text-2-color);
            font-size: var(--text-2-size);
            font-weight: var(--text-2-weight);
        }

        &_longer {
            width: 70%;
            background-color: rgb(44, 44, 44);
        }
    }
}