@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&family=Livvic:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,900&display=swap');


:root {
    --block: #1a1212;
    --block1: #2b1f1f;
    --block2: #3b2e2e;


    --block: #161616;
    --block1: #202020;
    --block2: #292929;


    --block-light: #838383;
    --second-color: #C09D68;
    --fz1: 1em;
    --fz2: 1.3em;
    --fz3: 1.6em;
    --fz4: 1.8em;
    --margin-display: 20px;


    --text-1-size: 1.6em;
    --text-1-family: livvic;
    --text-1-weight: 600;
    --text-1-color: #ffffff;


    --text-2-size: 1em;
    --text-2-family: livvic;
    --text-2-weight: 400;
    --text-2-color: var(--block-light);


    --text-3-size: 1.3em;
    --text-3-family: livvic;
    --text-3-weight: 500;
    --text-3-color: #fff;

    --text-4-size: 1em;
    --text-4-family: livvic;
    --text-4-weight: 400;
    --text-4-color: #fff;
}